@import "./_variables";
//Global line height.
$line-height-base: 1.54;
$font-family-base: Georgia,
'Arial Black';
$font-size-base: 16px;
@font-face {
    font-family: "Butler_Black_Stencil";
    src: url("../fonts/Butler_Black_Stencil.otf");
}

html,
body {
    color: $text-color;
    line-height: $line-height-base;
    font-family: $font-family-base;
    font-size: $font-size-base;
}

h1 {
    font-size: 92px;
    font-weight: 900;
}

.info {
    font-size: 26px;
}